            grid-row-gap: 16px;
        }
        .ps-section__mission {
            margin-bottom: 30px;
        }
        .ps-section__content {
            margin-bottom: 30px;
        }
    }

    @media screen and (min-width: 768px) and (max-width: 1024px) {
