        }
        .ps-section__brands {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-gap: 16px;

            > a {
                align-self: center;
                margin-bottom: 0;
                padding: 10px 0;
            }

            p {
                grid-column: 1/4;
            }
        }
        .ps-section__content {
            grid-template-columns: 1fr 1fr;
            grid-column-gap: 30px;
            padding-bottom: 30px;
        }

        .ps-section__wrapper {
