        grid-template-columns:  1fr 355px;

    }

    @media screen and (min-width: 1440px) {
        .ps-section__left {
            max-width: calc(1280px - 355px);
        }
    }

    @media screen and (min-width: 1600px) {
        grid-column-gap: 100px;
        .ps-section__left {
            max-width: calc(1510px - 355px);
        }
    }
}

.ps-home-search {
    min-height: calc(100vh + 80px);
    display: grid;
    position: relative;
    z-index: 10;

    &:before {
        content: '';
        @include max-area();
        z-index: -1;
        background-color: rgba(#000, 0.25);
    }

    .ps-section__stats {
        margin-bottom: 0;
        display: flex;
        justify-content: flex-start;
        align-content: center;
        grid-column-gap: 30px;
        color: #ffffff;
        font-size: 14px;

        i {
            margin-right: 12px;
        }
    }

    .ps-section__top {
        padding-bottom: 40px;

        h3 {
            margin-bottom: 25px;
            font-weight: 600;
            font-size: 50px;
            color: #ffffff;
            line-height: 1.1em;
            text-transform: capitalize;
        }

        p {
            margin-bottom: 0;
            color: #fff;
        }
    }

    .ps-section__form {
        padding-bottom: 20px;
    }

    .ps-section__wrapper {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        height: 100%;
    }

    @media screen and (min-width: 1024px) {

        .ps-section__content {
            max-width: 700px;
        }

        .ps-section__top {
            padding-bottom: 40px;

            h3 {
                margin-bottom: 10px;
                font-size: 50px;
                max-width: 500px;
            }

            p {
                margin-bottom: 0;
            }
        }

        .ps-section__form {
            padding-bottom: 40px;
        }
    }

    @media screen and (min-width: 1200px) {
        .ps-section__top {
            padding-bottom: 40px;

            h3 {
                font-size: 48px;
                max-width: 400px;
            }
        }

        .ps-section__wrapper {

            padding-bottom: 20px;
        }
    }

    @media screen and (min-width: 1366px) {
        .ps-section__top {
            padding-bottom: 60px;
        }

        .ps-section__wrapper {
            align-items: center;
            padding-bottom: 20px;
        }
    }

    @media screen and (min-width: 1440px) {
        .ps-section__top {
            padding-bottom: 60px;

            h3 {
                font-size: 48px;
                max-width: 500px;
            }

            p {
                font-size: 16px;
            }
        }
    }

    @media screen and (min-width: 1600px) {
        .ps-section__top {
            padding-bottom: 80px;

            h3 {
                font-size: 80px;
                max-width: 100%;
            }
        }
    }
}

.ps-submit-property {
    min-height: 500px;

    display: grid;
    text-align: center;

    .ps-btn {
        max-width: 300px;
        margin: 0 auto;
    }

    h3 {
        font-size: 36px;
        font-weight: 500;
        line-height: 1.2em;
        color: var(--color-heading);

        strong {
            font-weight: inherit;
            color: var(--color-success);
        }
    }

    p {
        line-height: 1.4em;
        margin-bottom: 40px;
    }

    .container {
        height: 100%;
        padding-top: 75px;
    }

    @media screen and (min-width: 768px) {
        .container {
            padding-top: 90px;
        }
        p {
            font-size: 18px;
            margin-bottom: 80px;
        }
        .ps-btn {
            height: 60px;
            font-size: 18px;
        }
    }

    @media screen and (min-width: 1024px) {

        .container {
            padding-top: 120px;
        }

        h3 {
            margin-bottom: 20px;
            font-size: 72px;
        }
    }

    @media screen and (min-width: 1366px) {

        .container {
            padding-top: 150px;
        }

        .ps-btn {
            height: 74px;
            font-size: 22px;
        }
    }

    @media screen and (min-width: 1680px) {
        min-height: 770px;

    }

    @media screen and (max-width: 480px) {
        h3 {
            margin: 0 auto 10px;
            max-width: 280px;
        }
        p {
            font-size: 14px;
        }
        .ps-btn {
            max-width: 240px;
        }
    }
}

.ps-trending-seacrch {
